.marker{
	width: 48px;
	background-color: white;
	border: 1px solid #999;
	border-radius: 4px;
	font-size: 18px;
	text-align: center;
	padding: 4px 0;
}

.marker:hover {
	width: 60px;
	font-size: 30px;
	-webkit-transition: 0.5s;
	-o-transition: 0.5s;
	transition: 0.5s;
}

.marker.selected {
	background-color: yellow;
	border-color: black;
}


